import React, {Component} from 'react'
import './Feed.css'
import text from '../external/text.json'
import AdSense from 'react-adsense'


class Feed extends Component {
    render() {
        return (
            <section id="post-list">
                <article>
                    <header>
                        <div className="post-title">
                            <span className="title"> Deploying React Applications to Github Pages </span>
                        </div>
                        <div className="user-info">
                            <span className="user-img"></span>
                            <div className="user-info-details">
                                <span>Richard Henrique</span>
                                <span className="postDateTime"> Oct 18, 2019 </span>
                            </div>
                        </div>
                    </header>
                    <section id="text">
                        {text.body.value.split('\n').map((item, i) => <p key={i}>{item}</p>)}
                    </section>
                    <footer>
                        <p>
                            <span> #nois#nois </span>
                            <span> #nois </span>
                            <span> #nois </span>
                            <span> #nois </span>
                        </p>
                    </footer>
                </article>
                <AdSense.Google
                client='ca-pub-9210675248914788'
                slot='7125141928'
                style={{ width: 400, height: 200 }}
                />
            </section>
        )
    }
}

export default Feed